$(document).ready(function() {

$("body").click(function(e){
  var dropdown = e.target.closest(".header__item");
  if(dropdown) {
    if(!$(dropdown).hasClass('open')){
    $(".open").removeClass('open');
   }
   $(dropdown).toggleClass('open');
  }else {
  $(".open").removeClass('open');
}
  var tableExpand = e.target.closest(".table__expand");
  if(tableExpand){
  var  table = tableExpand.closest(".table");
    $(table).toggleClass('table-close'); 
  }
})

var ctxLine = $("#myLineChart");
var ctxPie = $("#myPieChart");
var ctxDoughnut = $("#myDoughnutChart");
var myLineChart = new Chart(ctxLine, {
    type: 'line',
    fontColor: 'rgb(255, 255, 132)',
    data: {
        labels: [25, 50, 75, 100, 125, 150],
        datasets: [{
            label: 'Visitors',
            data: [7, 5, 3, 5, 10, 15],
            borderWidth: 1,
            backgroundColor:
                "rgba(217, 83, 79, .3)"
        },
        {
            label: 'Money',
            data: [2, 8, 9, 10, 15, 20],
            borderWidth: 1,
            backgroundColor:
                "rgba(92, 184, 92, .3)"
        }]
    },
    options: {
        responsive: true,
        scales: {
            yAxes: [{
                ticks: {
                    beginAtZero:true
                }
            }]
        }
    }
});
var myPieChart = new Chart(ctxPie,{
    type: 'pie',
    data: {
    labels: [
        "Apples",
        "Blueberries",
        "Bananas"
    ],
    datasets: [
        {
            data: [300, 50, 100],
            backgroundColor: [
                "rgba(217, 83, 79, .2)",
                "#36A2EB",
                "#FFCE56"
            ],
            hoverBackgroundColor: [
                "#FF6384",
                "#36A2EB",
                "#FFCE56"
            ]
        }]
},
    options: {
        responsive: true,
        maintainAspectRatio: false
    }
});
var myDoughnutChart = new Chart(ctxDoughnut,{
    type: 'doughnut',
    data: {
    labels: [
        "Russia",
        "Italy",
        "Germany",
        "Other",
        "United States"
    ],
    datasets: [
        {
            data: [25, 25, 40, 30, 30],
            backgroundColor: [
                "#65b5c2",
                "#2e7bad",
                "#23649e",
                "#63daed",
                "#3993bb"

            ],
            hoverBackgroundColor: [
                "#65b5c2",
                "#2e7bad",
                "#23649e",
                "#63daed",
                "#3993bb"
            ]
        }]
},
    options: {
        cutoutPercentage: 60,
        legend: {
          position: "right",
          labels: {
              usePointStyle: true
          }
        },
        responsive: true,
        maintainAspectRatio: false,
        animation:{
            animateScale:true
    }
    }
});

})


